* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  background: #eff3f7;
  height: 100%;
}

html, 
body, 
#potato, 
.container {
  height: 100%;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 20px;
  padding-top: 70px;
  width: 100%;
  border: 1px solid green;
}

.movies .movie {
  width: 45%;
  background: white;
  margin-bottom: 70px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  font-weight: 300;
  padding: 20px;
  border-radius: 5px;
  color: #b2b3bc;
  border: 1px solid red;

}

.movie img {
  position: relative;
  top: -50px;
  width: 100%;
  max-width: 150px;
  margin-right: 20px;
}

.movie .movie__title,
.movie .movie__year {
  margin: 0;
  font-weight: 300;
  border: 1px solid blue;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #2c2c2c;
}

.movie .movie__genres {
  list-style: none;
  padding: 0;
  /* margin: 0; */
  display: flex;
  margin: 5px 0px;
}

.movie__genres li {
  margin-right: 10px;
  font-size: 14px;
}